<template>
  <div class="sliderMenu">
    <a-layout-sider class="h100" v-model="collapsed" :trigger="null" collapsible>
      <a-button type="default" class="openMenu bt" @click="toggleCollapsed">
        <a-icon :type="collapsed?'menu-unfold':'menu-fold'"></a-icon>
        <span>{{collapsed?'展开':'收起'}}</span>
      </a-button>
      <a-menu mode="inline" theme="dark" :selectedKeys = "[defaultModule]" :defaultOpenKeys="$global.displaySliderSecondMenu">
        <template v-for="menu in menus">
          <a-menu-item :key="menu.moduleEn" v-if="menu.children.length === 0 || isDisplayThird(menu)" @click="toPlace(menu)">
            <a-icon :type="menu.icon"></a-icon>
            <span>{{menu.moduleCn}}</span>
          </a-menu-item>
          <a-sub-menu :key="menu.moduleEn" v-else>
            <span slot="title">
              <a-icon :type="menu.icon"></a-icon>
              <span>{{menu.moduleCn}}</span>
            </span>
              <template  v-for="item in menu.children" >
                  <a-menu-item :key="item.moduleEn" v-if="!item.migths||item.migths.includes(role)" @click="toPlace(item)">
                      {{item.moduleCn}}
                  </a-menu-item>
              </template>
          </a-sub-menu>
        </template>
      </a-menu>
      <!-- <div v-if="menus.length === 0">
        <a-icon type="cloud-server"></a-icon>
        <p>未发现数据</p>
      </div> -->
    </a-layout-sider>
  </div>
</template>
<script>
export default {
  name: "slider-menu",
  props:{
    defaultModule:String,
    menus:{
      type:Array,
      default:function(){
        return this.$fun.getMenuChildrenByModule(this);
      }
    },
  },
  data() {
    return {
      collapsed: false,
      // menus: [],
      role: sessionStorage.getItem('username'),
    };
  },
  created() {
    // this.menus = this.$fun.getMenuChildrenByModule(this);
    // this.defaultModule = this.$fun.getLastRoute(this);
  },
  methods: {
    toggleCollapsed() {
      this.collapsed = !this.collapsed;
    },
    toPlace(menu){
      this.$fun.toPlace(menu,this);
    },
    isDisplayThird(menu){
      return !this.$global.displaySliderSecondMenu.includes(menu.moduleEn)
    }
  }
};
</script>
<style>
/* .sliderMenu .ant-layout-sider {
    background-color: transparent;
} */
.sliderMenu .ant-layout-sider-children ul {
  background-color: transparent;
}
.sliderMenu .ant-layout-sider-children ul ul.ant-menu-inline.ant-menu-sub{
  background-color: #0e203c80;
}
.sliderMenu .ant-layout-sider-children ul li {
  text-align: left;
}
.sliderMenu .ant-layout-sider-children ul li:not(.ant-menu-submenu):hover {
  color: #5dc2fe;
  border-right: 3px solid #5dc2fe;
}
.sliderMenu .ant-layout-sider {
  /* background-color: rgba(0,21,41,.4); */
  background-color: rgba(93, 194, 254, 0.2);
}
.openMenu {
  margin-bottom: 16px;
  width: 100%;
  color: #fff;
}
</style>