/**
 * 日志查询相关模块接口
 */
import axios from 'axios'

export default {
    /**
     * 根据分析对象查询字段
     */
    getFieldsByObj(obj) {
        return axios.post("/server/search/searchFieldByAnalyseObject", {analysisObject: obj}).then(res => res.data);
    },
    /**
     * 日志查询接口
     */
    searchLog(params) {
        return axios.post("/server/search/searchLogs", params).then(res => res.data);
    },
    /**
     * 日志导出
     */
    exportLog(data) {
        // return axios.post("/server/search/exportLogsCsv", params).then(res => res.data);
        // responseType: 'arraybuffer'
        return axios({
            url: '/server/search/exportLogsCsv',
            method: 'post',
            data,
            responseType: 'arraybuffer' // 添加这句
        });
    },
    /**
     * 离线日志导出
     */
    logOfflineDownload(params) {
        return axios.post("/server/search/logOfflineDownload", params).then(res => res.data);
        /* return axios({
             url: '/server/search/logOfflineDownload',
             method: 'post',
             data,
             responseType: 'arraybuffer' // 添加这句
         });*/
    },
    /**
     * 镜内外日志导出
     */
    exportLogInOut(data) {
        // return axios.post("/server/search/exportLogsCsv", params).then(res => res.data);
        // responseType: 'arraybuffer'
        return axios({
            url: '/server/search/specialExportLogsCsv',
            method: 'post',
            data,
            responseType: 'arraybuffer' // 添加这句
        });
    },
    /**
     * 境内外同时分析
     */
    searchInOut(param) {
        return axios.post("/server/search/specialSearch", param).then(res => res.data);
    },
    /**
     * 根据查询字段
     */
    searchFields(param) {
        return axios.post("/server/search/searchFieldByExample", param).then(res => res.data);
    },
    /**
     * 统计查询结果
     */
    tj_result(param) {
        return axios.post("/server/search/searchOverView", param).then(res => res.data);
    },

    /**
     * 统计查询结果详情
     */
    detail_city(param) {
        return axios.post("/server/search/getCityDetail", param).then(res => res.data);
    },
    /**
     * 统计查询结果详情
     */
    detail_ip(param) {
        return axios.post("/server/search/getIpDetail", param).then(res => res.data);
    },
    /**
     * 统计查询结果详情
     */
    detail_country(param) {
        return axios.post("/server/search/getCountryDetail", param).then(res => res.data);
    },
    /**
     * 统计查询结果详情
     */
    detail_email(param) {
        return axios.post("/server/search/getEmailDetail", param).then(res => res.data);
    },
    /**
     * 保存缓存结果
     * @param param
     * @returns {Promise<AxiosResponse<T> | never>}
     */
    saveResult(param) {
        return axios.post("/server/search/saveResult", param).then(res => res.data);
    },

    deleteResult(param) {
        return axios.post("/server/search/deleteResult", param).then(res => res.data);
    },

    /**
     * 详情快速导出
     */
    detail_quickly(param) {
        return axios({
            url: '/server/search/exportDetail',
            method: 'post',
            data: param,
            responseType: 'arraybuffer' // 添加这句
        });
    },

}

export function downloadFile(file, filename = '') {
    console.log(file)
    let blob = new Blob(
        [file.data],
        {
            type: 'text/plain'
        });

    let downloadElement = document.createElement('a');
    let href = window.URL.createObjectURL(blob); //创建下载的链接
    console.log("file", file)
    console.log("blob", blob);
    downloadElement.href = href;
    downloadElement.download = filename; //下载后文件名
    document.body.appendChild(downloadElement);
    downloadElement.click(); //点击下载
    document.body.removeChild(downloadElement); //下载完成移除元素
    window.URL.revokeObjectURL(href); //释放掉blob对象
}

export function downloadFileOffline(url, filename = '') {
    let downloadElement = document.createElement('a');
    // let href = window.URL.createObjectURL(blob); //创建下载的链接
    // console.log(blob);
    downloadElement.href = url;
    downloadElement.download = filename; //下载后文件名
    document.body.appendChild(downloadElement);
    downloadElement.click(); //点击下载
    document.body.removeChild(downloadElement); //下载完成移除元素
}
