import loadingComponent from './loading/loading.vue'
import sliderMenuComponent from './sliderMenu/sliderMenu.vue'
import breadcrumbComponent from './breadcrumb/breadcrumb.vue'
import mytabComponent from './mytab/my-tab.vue'
import customModalComponent from './customModal/customModal.vue'

const components = {
    install:function(Vue){
        // 全局loading组件
        Vue.component('my-loading',loadingComponent);
        // 全局侧边栏菜单组件
        Vue.component('slider-menu',sliderMenuComponent);
        // 全局面包屑组件
        Vue.component('my-breadcrumb',breadcrumbComponent);
        // 全局tab栏组件
        Vue.component('my-tab',mytabComponent);
        // 全局自定义模态框组件
        Vue.component('custom-modal',customModalComponent);
    }
}

export default components