<template>
    <div class="mytab">
        <div class="tabContainer">
            <ul class="clearfix">
                <li v-for="tab in tabs" :key="tab.key" :class="{'selected':tab.key === defaultKey}" @click="tabClick(tab)">{{tab.label}}</li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:'mytab',
    props:{
        tabs:Array,
        defaultKey:String
    },
    data(){
        return {
            
        }
    },
    methods:{
        tabClick(tab){
            this.$emit('tabClick',tab)
        }
    }
}
</script>
<style>
.tabContainer {
    height: 40px;
    /* background-color: #192e4e; */
     background-image: radial-gradient(circle at left, #02608b, #192e4e, #02608b);
    line-height: 40px;
}
.mytab .tabContainer ul li {
    float: left;
    list-style: none;
    font-size: 16px;
    min-width: 100px;
    text-align: center;
    cursor: pointer;
}
.selected {
    background-color: rgba(93, 194, 254, 0.4);
}
</style>