/* eslint-disable no-unused-vars */
import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../views/login/login'
import store from '../store/index'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        redirect: '/login'
    },
    {
        path: '/login',
        name: 'login',
        component: login,
    },
    {
        path: '/home',
        name: 'home',
        component: () => import('../views/home/home.vue')
    },
    {
        path: '/business',
        component: () => import('../views/business/business.vue'),
        children: [
            {
                path: '',
                redirect: 'search'
            },
            {
                path: 'search',
                component: () => import('../views/business/search/search.vue'),
                children: [
                    // {
                    //     path: 'searchLog',
                    //     name: 'searchLog',
                    //     component: () => import('../views/business/search/searchLog/searchLog.vue'),
                    // }, 
                    {
                        path: 'logSearch',
                        name: 'logSearch',
                        component: () => import('../views/business/search/logSearch.vue'),
                    }, 
                    // {
                    //     path: 'searchNginx',
                    //     name: 'searchNginx',
                    //     component: () => import('../views/business/search/searchNginx/searchNginx.vue'),
                    // }
                ]
            },
            {
                path: 'monitoring',
                name: 'monitoring',
                component: () => import('../views/business/monitoring/monitoring.vue')
            },
            {
                path: 'analyze',
                component: () => import('../views/business/analyze/analyze.vue'),
                children: [
                    {
                        path: 'basic',
                        component: () => import('../views/business/analyze/basic/basic.vue'),
                        children: [
                            {
                                path: '',
                                redirect: 'blastingAnalyze',
                            },
                            {
                                path: 'blastingAnalyze',
                                name: 'blastingAnalyze',
                                component: () => import('../views/business/analyze/basic/basicItem/blastingAnalyze.vue'),
                            }, {
                                path: 'illegalAnalyze',
                                name: 'illegalAnalyze',
                                component: () => import('../views/business/analyze/basic/basicItem/illegalAnalyze.vue'),
                            }, {
                                path: 'distanceAnalyze',
                                name: 'distanceAnalyze',
                                component: () => import('../views/business/analyze/basic/basicItem/distanceAnalyze.vue'),
                            },{
                                path: 'distanceLogAnalyze',
                                name: 'distanceLogAnalyze',
                                component: () => import('../views/business/analyze/basic/basicItem/distanceLogAnalyze.vue'),
                            },{
                                path: 'illegalAccessAnalyze',
                                name: 'illegalAccessAnalyze',
                                component: () => import('../views/business/analyze/basic/basicItem/illegalAccessAnalyze.vue'),
                            },{
                                path: 'distanceAccessAnalyze',
                                name: 'distanceAccessAnalyze',
                                component: () => import('../views/business/analyze/basic/basicItem/distanceAccessAnalyze.vue'),
                            },
                        ]
                    },
                    {
                        path: 'special',
                        component: () => import('../views/business/analyze/special/special.vue'),
                        children:[
                            {
                                path: 'webLoginAnalyze',
                                name: 'webLoginAnalyze',
                                component: () => import('../views/business/analyze/special/specialItem/webLoginAnalyze.vue'),
                            }
                        ]
                    }
                ]
            },
            {
                path: 'collect',
                name: 'collect',
                component: () => import('../views/business/collect/collect.vue'),
                children: [
                    {
                        path: 'task',
                        name: 'task',
                        component: () => import('../views/business/collect/task/task.vue'),
                    },
                    {
                        path: 'fileRecordsDetail',
                        name: 'fileRecordsDetail',
                        component: () => import('../views/business/collect/task/fileRecordsDetail.vue'),
                    },
                    {
                        path: 'project',
                        name: 'project',
                        component: () => import('../views/business/collect/project/project.vue'),
                        children: [
                            {
                                path: 'project-list',
                                name: 'project-list',
                                component: () => import('../views/business/collect/project/project-list.vue'),
                            },
                            {
                                path: 'project-opt',
                                name: 'project-opt',
                                component: () => import('../views/business/collect/project/project-opt.vue'),
                            },
                            {
                                path: 'project-detail',
                                name: 'project-detail',
                                component: () => import('../views/business/collect/project/project-detail.vue'),
                            },
                        ]
                    },

                ]
            },
            {
                path: 'setting',
                component: () => import('../views/business/setting/setting.vue'),
                children: [
                    {
                        path: '',
                        redirect: 'newField'
                    },
                    {
                        path: 'newField',
                        name: 'newField',
                        component: () => import('../views/business/setting/channel/newField.vue'),
                        children: []
                    },
                    {
                        path: 'channel',
                        name: 'channel',
                        component: () => import('../views/business/setting/channel/channel.vue'),
                        children: []
                    },
                    {
                        path: 'field',
                        name: 'field',
                        component: () => import('../views/business/setting/channel/field.vue'),
                        children: []
                    },
                    {
                        path: 'field/:type',
                        name: 'field-opt',
                        component: () => import('../views/business/setting/channel/field-opt.vue'),
                        children: []
                    },
                    {
                        path: 'baseRegExp',
                        name: 'baseRegExp',
                        component: () => import('../views/business/setting/baseRegExp/baseRegExp.vue'),
                        children: []
                    },
                    {
                        path: 'builtFuns',
                        name: 'builtFuns',
                        component: () => import('../views/business/setting/builtFuns/builtFun.vue'),
                        children: []
                    },
                    {
                        path: 'expression',
                        name: 'expression',
                        component: () => import('../views/business/setting/expression/expression.vue'),
                        children: []
                    },
                    {
                        path: 'dictionary',
                        name: 'dictionary',
                        component: () => import('../views/business/setting/dictionary/dictionary.vue'),
                        children: []
                    }, {
                        path: 'interface',
                        name: 'interface',
                        component: () => import('../views/business/setting/interface/interface.vue'),
                        children: []
                    },
                    /*{
                        path: 'label',
                        name: 'label',
                        component: () => import('../views/business/setting/label/label.vue'),
                        children: []
                    },*/
                    {
                        path: 'config',
                        component: () => import('../views/business/setting/config/config.vue'),
                        children: [
                            {
                                path: '',
                                redirect: 'unit'
                            },
                            {
                                path: 'unit',
                                name: 'unit',
                                component: () => import('../views/business/setting/config/configItem/unit.vue'),
                            },
                            {
                                path: 'analyzeObject',
                                name: 'analyzeObject',
                                component: () => import('../views/business/setting/config/configItem/analyzeObject.vue'),
                            },
                            {
                                path: 'rule',
                                name: 'rule',
                                component: () => import('../views/business/setting/config/configItem/rule.vue'),
                            },
                            {
                                path: 'ruleGroup',
                                name: 'ruleGroup',
                                component: () => import('../views/business/setting/config/configItem/ruleGroup.vue'),
                            }, {
                                path: 'application',
                                name: 'application',
                                component: () => import('../views/business/setting/config/configItem/application.vue'),
                            },
                        ]
                    },
                ]
            },
        ]
    }
]
const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

// 路由拦截
router.beforeEach((to, from, next) => {
    if (to.name !== 'login') {
        store.commit('setLoading', true)
    }
    // console.log(from)
    next();
})
router.afterEach((to, from) => {
    // console.log(to)
    // console.log(from)
    if (to.name !== 'login') {
        setTimeout(() => {
            store.commit('setLoading', false)
        }, 300);
    }
})
// 修改路由重复点击报错
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err);
}
export default router
