<template>
    <div class="breacrumb al">
        <a-breadcrumb>
            <a-breadcrumb-item>业务模块</a-breadcrumb-item>
            <a-breadcrumb-item>{{secondCn}}</a-breadcrumb-item>
            <a-breadcrumb-item v-if="thirdCn">{{thirdCn}}</a-breadcrumb-item>
            <a-breadcrumb-item v-for="name of extra" :key="name">{{name}}</a-breadcrumb-item>
        </a-breadcrumb>
    </div>
</template>
<script>
export default {
    name:'my-breadcrumb',
    props:{
        extra:{
            type:Array,
            default:function(){
                return []
            }
        }
    },
    data(){
        return {
            routeArr:[],
            secondCn:"",
            thirdCn:""
        }
    },
    created(){
        this.routeArr = this.$route.path.split('/');
        this.secondCn = this.$global.menuList.find(list => list.moduleEn === this.routeArr[2]).moduleCn;
        let secondChildren = this.$global.menuList.find(list => list.moduleEn === this.routeArr[2]).children;
        if(secondChildren.length>0){
            let third = secondChildren.find(list => list.moduleEn === this.routeArr[3]);
            this.thirdCn = third?third.moduleCn:"";
        }
    }
}
</script>
<style>
.breacrumb {
    margin: 15px 0;
}
.breacrumb .ant-breadcrumb > span,.breacrumb .ant-breadcrumb-separator {
    color: #fff;
}
</style>