/**
 * 存储所有全局管道（过滤器），并导出在Vue中全局注册
 */
const filters = {
    /**
     * @description 此处用于过滤器使用，处理时间函数，将时间戳、标准时间格式 转换 字符串 如2009-03-06 or 2009-03-06 12:36:20
     * @param {number|Date} 不需要传入
     * @param {number} 可选择传入 0：截止到日 1：截止到秒 默认未0
     */
    dateFormatter(val, type = 0) {
        if(!val){
            return val
        }
        let str;
        let time = new Date(val);
        var year = time.getFullYear();
        var month = time.getMonth() + 1;
        var date = time.getDate();
        var hTemp = time.getHours();
        var h = hTemp >= 10 ? hTemp : '0' + hTemp;
        var mTemp = time.getMinutes();
        var m = mTemp >= 10 ? mTemp : '0' + mTemp;
        var sTemp = time.getSeconds();
        var s = sTemp >= 10 ? sTemp : '0' + sTemp;
        if (type === 1) {
            str = `${year}-${month}-${date} ${h}:${m}:${s}`;
        } else {
            str = `${year}-${month}-${date}`;
        }
        return str
    },
    // 处理下载状态
    parseStatus(status){
        if (status === "0"){
            status = '进行中'
        }else if (status === "1"){
            status = '完成'
        }else if (status === "2"){
            status = '失败'
        }
        return status
    },
    /**
     * 根据分析对象id获取分析对象的中文名
     */
    getObjectCnById(id,context){
        if(!id){
            return id
        }
        if(!context){
            console.error('请传入vue实例对象[filter.js]')
            return id
        }
        let data = context.$store.state.objectData;
        if(data.length === 0){
            return 
        }
        let temp = data.find(list => list.id === id);
        let objectName = temp?temp.objectName:"";
        return objectName;
    },
    /**
     * 根据规则组id获取规则组的中文名
     */
    getRuleGroupCnById(id,context){
        if(!id){
            return id
        }
        if(!context){
            console.error('请传入vue实例对象[filter.js]')
            return id
        }
        let data = context.$store.state.ruleGroupData;
        if(data.length === 0){
            return 
        }
        let temp = data.find(list => list.id === id);
        let rulGroupName = temp?temp.name:"";
        return rulGroupName;
    },
    /**
     * 根据单位en获取单位的中文名
     */
    getUnitCnById(id,context){
        if(!id){
            return id
        }
        if(!context){
            console.error('请传入vue实例对象[filter.js]')
            return id
        }
        let data = context.$store.state.unitData;
        if(data.length === 0){
            return 
        }
        let temp = data.find(list => list.id === id);
        let unitName = temp?temp.cname:"";
        return unitName;
    },
    /**
     * 根据单位id获取单位的中文名
     */
    getUnitCnByEn(en,context){
        if(!en){
            return en
        }
        if(!context){
            console.error('请传入vue实例对象[filter.js]')
            return en
        }
        let data = context.$store.state.unitData;
        if(data.length === 0){
            return 
        }
        let temp = data.find(list => list.ename === en)
        let unitName = temp?temp.cname:"";
        return unitName;
    }
}
export default filters