/**
 * analyze模块相关接口
 */
import axios from 'axios'

export default {
    /**
     * 爆破登录分析接口
     */
    getForceLoginAgg(params){
        return axios.post("/server/logana/getForceLoginAgg",params).then(res => res.data);
    },
    /**
     * 非法登录分析接口
     */
    getUnormalLoginAgg(params){
        return axios.post("/server/logana/getUnormalLoginAgg",params).then(res => res.data);
    },
    /**
     * 异地登陆查询
     */
    searchDistanceList(params){
        return axios.post('/server/logana/getMultiRegionAgg',params).then(res =>res.data)
    },
  /**
   * 分析对象接口
   */
  getAnalysisObj(params){
    return axios.get('/server/analysisObject/selectAnalyObjectByUnit',{params}).then(res =>res.data)
  },
  /**
     * 异地登陆查询
     */
  getEmailDetail(params){
      return axios.post('/server/logana/getAnotherPlaceDetail',params).then(res =>res.data)
  },
/**
 * 分析对象接口
 */
selectByObjectName(params){
    return axios.get('/server/analysisObject/selectByObjectName',{params}).then(res =>res.data)
},
}


