import axios from 'axios'

// 请求拦截器
axios.interceptors.request.use(config => {
    // console.log(config)
    return config
},error => {
    // 请求失败时候处理
    // console.log(error)
    return Promise.reject(error)
})

// 响应拦截器
axios.interceptors.response.use(res => {
    // console.log(res)
    return res
},error => {
    // 请求失败时候处理
    // console.log(error)
    return Promise.reject(error)
})