<template>
  <div class="login">
    <div class="top al">
      {{$global.projectNameEn}}
      <span style="font-size:12px;color:#eee">{{$global.version}}</span>
    </div>
    <div class="loginCard clearfix">
      <div class="cardPic fl"></div>
      <div class="cardContent fl">
        <a-card
          style="height:100%;background-color:rgba(255,255,255,.1);border:none"
          :bodyStyle="loginCardStyle"
        >
          <div class="title">
            <p class="ac">{{$global.projectName}}</p>
          </div>
          <div class="form">
            <a-form layout="vertical" :form="loginForm" @submit="loginSubmit">
              <a-form-item label="用户名：">
                <a-input placeholder="请输入用户名" size="large" v-decorator="loginFormRule.username" autocomplete="off">
                  <a-icon slot="prefix" type="user" style="color:#dedede"></a-icon>
                </a-input>
              </a-form-item>
              <a-form-item label="密码：">
                <a-input type="password" placeholder="请输入密码" size="large" v-decorator="loginFormRule.password"  autocomplete="off">
                  <a-icon slot="prefix" type="lock" style="color:#dedede"></a-icon>
                </a-input>
              </a-form-item>
              <a-form-item style="padding: 2vmin 0;">
                <a-button type="primary" html-type="submit" class="loginButton" @>登录</a-button>
              </a-form-item>
            </a-form>
          </div>
        </a-card>
      </div>
    </div>
    <div class="footer ac">&copy;2019-2020&nbsp;济南互信软件有限公司</div>
  </div>
</template>
<script>
export default {
  name: "login",
  beforeCreate(){
      this.loginForm = this.$form.createForm(this,{
          name:'login_control'
      })
  },
  data() {
    return {
      loginCardStyle: {
        color: "#fff",
        height: "100%",
      },
      // 定义表单规则
      loginFormRule: {
          username:[
              'username',
              {
                  rules:[
                      {
                          required:true,
                          message:'请输入用户名！'
                      }
                  ]
              }
          ],
          password:[
              'password',
              {
                  rules:[
                      {
                          required:true,
                          message:'请输入密码！'
                      }
                  ]
              }
          ],
      }
    };
  },
  methods:{
      loginSubmit(e){
          e.preventDefault();
          this.loginForm.validateFields((err,values) => {
              if (!err) {
                  console.log(values);
                  const user = Object.assign({}, values);
                  if (user.username === '' || user.password === '') {
                      return false;
                  }
                  // 调用登录接口
                  this.$API.userLogin(user).then(res => {
                      if (res.code === 1000) {
                          sessionStorage.setItem('username', user.username);
                          localStorage.setItem('token', res.data);
                          // window.location.href = '/business/search/searchLog';
                          this.$store.dispatch("getUnitData", this);
                          this.$router.push({name:'logSearch'});
                      } else {
                          localStorage.clear();
                          sessionStorage.clear();
                          this.$message.error(res.message);
                      }
                  })
              } else {
                  this.$message.error('请填写正确格式的密码')
              }
              /*if(!err){
                // 校验通过
                console.log(values);
                this.$router.push({name:'searchLog'});
              } else {
                this.$Message.error('用户名密码输入有误！');
              }*/
          })
      }
  }
};
</script>
<style>
.login {
  width: 100vw;
  height: 100vh;
  min-width: 1024px;
  min-height: 768px;
  overflow: auto;
  background: url(/static/imagin/bg1.png) no-repeat;
  background-size: cover;
}
.login::-webkit-scrollbar {
  display: none;
}
.loginCard {
  width: 32vw;
  height: 42vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 640px;
  min-height: 430px;
}
.loginCard .cardPic {
  width: 42%;
  height: 100%;
  border: 1px solid #aaa;
  background: url(/static/imagin/3.jpg) no-repeat 30%;
  background-size: cover;
  border-radius: 4px;
}
.loginCard .cardContent {
  width: 58%;
  height: 100%;
  color: #fff;
  border-radius: 4px;
}
.loginCard .cardContent .title {
  color: #fff;
  font-size: 2.6vmin;
  margin-bottom: 4vmin;
  letter-spacing: 6px;
  font-family: cursive;
}
.login .form .ant-form-item {
  margin-bottom: 24px;
}
.form .ant-form-item-label > label {
  color: #dedede;
  font-size: 1.2vmin;
  font-weight: 700;
}
.form .loginButton {
  width: 100%;
  font-size: 1.2vmin;
  font-weight: 700;
  height: 40px;
}
.login .top,
.login .footer {
  height: 64px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  position: fixed;
  color: #fff;
  line-height: 64px;
}
.login .top {
  top: 0;
  font-size: 2.4vmin;
  padding-left: 40px;
}
.login .footer {
  bottom: 0;
}
.login .ant-input {
    border: 1px solid #008cff;
    background: #042031;
    color: #fff;
}
.login .ant-input::-webkit-input-placeholder {
    color:#dedede;
    font-size: 1.2vmin;
    opacity: .6;
}
.login .has-error .ant-input-affix-wrapper .ant-input, .login .has-error .ant-input-affix-wrapper .ant-input:hover {
    background: #042031;
}
</style>