<template>
  <div class="loading">
    <div class="chart-loader" v-show="loadingVisible">
      <div class="loader"></div>
      <div class="loaderText" :style="textStyle" v-if="loadingText">{{loadingText}}</div>
        <div class="mask" :style="maskStyle"></div>
    </div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "my-loading",
  props: {
    loadingVisible: {
      type: Boolean,
      default: false,
    },
    loadingText: {
      type: String,
      default: "努力加载中...",
    },
    textStyle: {
      type: Object,
      default() {
        return {
          "font-weight": 700,
        };
      },
    },
    maskStyle: {
      type: Object,
    },
  },
};
</script>
<style>
.loading {
  width: 100%;
  height: 100%;
  /* position: fixed; */
  overflow-x: hidden
}
.loading .mask {
  width: 100%;
  height: 100%;
  /* position: fixed; */
  z-index:98;
  /* background-color: rgba(255, 255, 255, 0.3); */
}
.chart-loader {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0);
  transition: all 0.8s;
}
.chart-loader .loaderText {
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translateX(-50%);
}
.chart-loader .loader {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  border: 3px solid transparent;
  border-top-color: #3498db;
  border-radius: 50% !important;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
.chart-loader .loader:before {
  content: "";
  position: absolute;
  top: 3px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border: 3px solid transparent;
  border-top-color: #e74c3c;
  border-radius: 50% !important;
  -webkit-animation: spin 3s linear infinite;
  animation: spin 3s linear infinite;
}
.chart-loader .loader:after {
  content: "";
  position: absolute;
  top: 9px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 3px solid transparent;
  border-top-color: #f9c922;
  border-radius: 50% !important;
  -webkit-animation: spin 1.5s linear infinite;
  animation: spin 1.5s linear infinite;
}
.chart-done .chart-loader {
  display: none;
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
</style>