<template>
  <div class="customModal" :style="customStyle" v-show="visible">
    <!-- 自定义模态框组件 -->
    <div class="modalContent">
      <header class="clearfix" v-drag="{bol:dragable,domIndex:2}" :style="headerStyle">
        <div class="title fl">
          <slot name="header">{{title}}</slot>
        </div>
        <div class="meta fr">
          <slot name="header-meta">
            <a-icon type="close"></a-icon>
          </slot>
        </div>
      </header>
      <main :style="bodyStyle">
        <slot>main</slot>
      </main>
      <footer :style="footerStyle">
        <slot name="footer"></slot>
      </footer>
    </div>
  </div>
</template>
<script>
export default {
  name: "customModal",
  props: {
    //  开启显示
    visible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 是否可拖拽
    dragable: {
      type: Boolean,
      default: false,
    },
    // 标题
    title: {
      type: String,
      default: "标题",
    },
    // 样式
    headerStyle: {
      type: Object,
      default() {
        return {
          cursor: this.dragable ? "move" : "inherit",
          borderBottom: "1px solid #e6f7ff",
        };
      },
    },
    footerStyle: {
      type: Object,
      default() {
        return {
          borderTop: "1px solid #e6f7ff",
        };
      },
    },
    bodyStyle: {
      type: Object,
      default() {
        return {
          padding: "24px",
        };
      },
    },
  },
  data() {
    return {
      customStyle: {
        top: "25%",
        left: "25%",

        width: "1200px",
        maxHeight: "800px",
        overflowY: "auto",
        overflowX: "hidden",
        backgroundImage:
          "radial-gradient(circle at left center, rgba(0, 0, 0, 1), #053060, #091528)",
        zIndex:'999'
      },
    };
  },
};
</script>
<style>
.customModal {
  position: fixed;
  /* transform: translate(-50%, -50%); */
  border-radius: 4px;
}
.customModal .modalContent header,
.customModal .modalContent footer {
  padding: 12px;
}
.customModal .modalContent main {
  padding: 12px;
}
.modalContent .meta .anticon {
    cursor: pointer;
    padding: 2px;
    margin:0 2px;
}
.modalContent .meta .anticon:hover  {
    background-color:rgba(93, 194, 254, 0.3)
}
</style>
